*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    max-width: 100%;
    /* transition:0.5s ease-in-out; */
    font-family: 'Arial Unicode MS', sans-serif;
}
body{
    background: #262626;
}
::-webkit-scrollbar {
    display: block;
    width: 10px;
    height: 15px;
}
::-webkit-scrollbar-track {
    border-radius: 10px;
    width:10px;
    background-color: rgba(255, 255, 255, 0.1);
}
::-webkit-scrollbar-thumb {
    background-image: linear-gradient(45deg, #00aeff, #ff8eec);
    border-radius: 10px;
    -webkit-box-shadow: rgba(0,0,0,.12) 0 3px 13px 1px;
}
::-webkit-scrollbar-thumb:hover{
    background-image: linear-gradient(45deg, #0084c2, #ff0ed7);
}
.Main_Container{
    height:200vh;
    width:100vw;
    align-items: center;
    justify-content: center;
}
.Page_1{
    height: 100vh;
    width:100%;
    background: url('./Images/N2.svg');
    background-size: cover;


}
.Sub_Container{
    display:grid;
    align-items: center;
    justify-content: center;
    grid-template-columns: 40% 60%;
    grid-template-areas: "Text images";
    width: 100%;

   
}
.All_info{
    margin-top:-20em;
    grid-area: Text;
    display: grid;
    width: 100%;

    align-items: center;
    justify-content: center;
    grid-gap:1em;
    color: white;
}
.Heading_Symbol{
    text-align: left;

}
.Heading_Symbol h1{
    font-size: 50px;
    text-transform: uppercase;
}
.Heading_Symbol h2{
    font-size: 30px;
}
.Sub_info{
    color: white;
    align-items: center;
    justify-content: center;
    text-align: left;
}
.Sub_info h2{
    font-size: 20px;
}

.All_Charts{
    grid-area: images;

}
.First_Plot_Tab, .Second_Plot_Tab, .First_Plot_Phone, .Second_Plot_Phone{
  display:none;
}
.Page_2{
    height: 107vh;
    width:100%;
    background: linear-gradient(#505075,#1b1b3f);
}
.Sub_news{
    display:grid;
    align-items: center;
    justify-content: center;
    margin-top: -2.9em;
    margin-left: 5em;
    height: 100%;
   
}
.Sub_news h1{
    width:90%;
    color: white;
    font-size: 50px;
    text-align: left;
}
.All_news{
    display: grid;
    align-items: center;
    justify-content: center;
    grid-gap:20px;
}
.All_news a{
    font-size: 15px;
    text-decoration: none;
    color: white;
    text-align: left;

}
.All_news a:hover{
    color:rgb(0, 204, 204);
}
.Source_news{
    display: grid;
    grid-gap:10px;
}
.All_Links{
    display: grid;
    align-items: center;
    justify-content: center;
    margin-left: -2em;
}
.Home_back{
    text-decoration: none;
    font-size: 30px;
    color: white;
}
.Home_back:hover{
    color:rgb(185, 185, 185);
    transition: 0.25s ease;
}
.Image_links{
    width:50vw;
    display: grid;
    grid-template-columns: 50% 50%;
    align-items: center;
    justify-content: center;
}
.Image_links a {
    text-decoration: none;
 
}
.Image_links a h2{
    opacity:0;
    color:white;
}
.Hover_name,.Hover_name1{
    opacity: 0;
}
.Hover_name:hover,.Hover_name1:hover{
    opacity: 1;
}
.Github{
    height:50px;
    border-radius: 50%;
    padding: 5px;
    
    
}
.Github:hover{
    transform:rotate(360deg);
    transition: 1s ease;
    border: 2px solid white;
    visibility: visible;
    opacity: 1;
}
.Portfolio{
    height: 50px;
    margin-top: 8px;
    border-radius: 50%;

}
.Portfolio:hover{
    transform:rotate(360deg);
    transition: 1s ease;
    border: 2px solid white;

}
.Ending{
    font-size: 15px;
    color: rgb(177, 177, 177);
}
.Ending:hover{
    transition: 0.5s ease;
    color:rgb(156, 156, 156);
    cursor:pointer;
}
@media screen and (max-width:1030px) and (min-width:375px){
    .Page_1{
        height: 100vh;
        width:100%;
        background: url('./Images/N3.svg');
        background-size: cover;
    
    }
    .Main_Container{
        height:190vh;
    }
    .Sub_Container{
        width: 100%;
        display:grid;
        align-items: center;
        justify-content: center;
        grid-template-rows: 40% 60%;
        grid-template-columns: 100%;
        grid-gap:4em;
        grid-template-areas: "Text"
                                "Chart";
    }
    .All_info{
        margin-top: 0em;
        margin-left: -16em;
        width:100%;
        grid-area: Text;
    }
    .All_Charts{
        grid-area:Chart;
        width:100%;
    }
    .First_Plot, .Second_Plot{
        display:none;
    }
    .First_Plot_Tab, .Second_Plot_Tab{
        display:block;
    }
    .Page_2{
        margin-top: 2.5em;
        height: 90vh;
    }
    .Sub_news{
        margin-left: 6em;
    }
    .Sub_news h1{
        margin-left: 30px;
        font-size: 70px;
    }
    .All_news{
        margin-left: 30px;
        width:80%;
    }
    .All_Links{
        margin-left: -2.5em;
    }
    .Portfolio{
        margin-top: 5px;
    }

}
